@import '@carbon/themes/scss/themes';

$carbon--theme: $carbon--theme--g90;
@include carbon--theme();

@import 'carbon-components/scss/globals/scss/styles.scss';

.icon-button {
  display: flex;
  justify-content: center;
  align-items: center;
  background: none;
  height: 3rem;
  width: 3rem;
  padding: 0;
  border: none;
}

.icon-button:hover {
  background-color: #353535;
}

.side-panel {
  display: flex;
  flex-direction: column;
  padding: 64px 16px;
  align-items: center;
  justify-content: space-between;
  z-index: 0;
}

.chat {
  background-color: $ui-01;
  height: 100%;
  padding: 0.2rem;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.chat p {
  border-radius: 1rem;
  padding: 0.5rem 1rem;
  margin: 0.2rem;
  max-width: 60%;
}

.watson {
  align-self: flex-start;
  background-color: $interactive-03;
  color: $inverse-01;
}

.user {
  align-self: flex-end;
  background-color: $interactive-04;
}

.renew-session {
  position: absolute;
  cursor: pointer;
  top: 0;
  right: 2rem;
  background: $ui-04;
  border: none;
  padding: 0.3rem;
  opacity: 0.8;
}

.renew-session:hover {
  background-color: $ui-05;
}

.send-message {
  position: absolute;
  right: 0.5rem;
  background: none;
  border: none;
  top: calc(1.25rem - 16px);
  filter: invert(1);
  cursor: pointer;
  animation: appear 0.3s ease;
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
